import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import DashboardIcon from '@material-ui/icons/Dashboard';
import Assessment from '@material-ui/icons/Assessment';
import Commute from '@material-ui/icons/Commute';
import SentimentVerySatisfiedIcon from '@material-ui/icons/SentimentVerySatisfied';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import LayersIcon from '@material-ui/icons/Layers';
import AssignmentIcon from '@material-ui/icons/Assignment';
import { Switch } from '@material-ui/core';
const sideOptions = [{
    title: "Summary",
    path: "home"
},
{
    title: "Mobility",
    path: "Mobility"
},
{
    title: "Sentiment",
    path: "Sentiment"
}]
export const mainListItems = (
    
    <div>
        {sideOptions.map((option, index) => (
            <ListItem button key={option.title} component="a" href={option.path === "home" ? "\\" : option.path}>
                <ListItemIcon>
              { option.title==='Summary'? <Assessment />
                : option.title==='Mobility' ? <Commute /> 
                : option.title==='Sentiment' ? <SentimentVerySatisfiedIcon />
                :''

              }
              
              
                </ListItemIcon>
                <ListItemText primary={option.title} />
            </ListItem>
        ))}
        {/* <ListItem button key={text.topic} component="a" href={text.path === "home" ? "\\" : text.path}>
            <ListItemIcon>
                <DashboardIcon />
            </ListItemIcon>
            <ListItemText primary="Dashboard" />
        </ListItem>
        <ListItem button>
            <ListItemIcon>
                <ShoppingCartIcon />
            </ListItemIcon>
            <ListItemText primary="Sentiment" />
        </ListItem>
        <ListItem button>
            <ListItemIcon>
                <PeopleIcon />
            </ListItemIcon>
            <ListItemText primary="Mobility" />
        </ListItem>
        <ListItem button>
            <ListItemIcon>
                <BarChartIcon />
            </ListItemIcon>
            <ListItemText primary="About" />
        </ListItem>
        {/* <ListItem button>
            <ListItemIcon>
                <LayersIcon />
            </ListItemIcon>
            <ListItemText primary="Integrations" />
        </ListItem> */}
    </div>
);

export const secondaryListItems = (
    <div>
        <ListSubheader inset>Saved reports</ListSubheader>
        <ListItem button>
            <ListItemIcon>
                <AssignmentIcon />
            </ListItemIcon>
            <ListItemText primary="Current month" />
        </ListItem>
        <ListItem button>
            <ListItemIcon>
                <AssignmentIcon />
            </ListItemIcon>
            <ListItemText primary="Last quarter" />
        </ListItem>
        <ListItem button>
            <ListItemIcon>
                <AssignmentIcon />
            </ListItemIcon>
            <ListItemText primary="Year-end sale" />
        </ListItem>
    </div>
);