import Typography from '@material-ui/core/Typography';
import React, { useRef, useLayoutEffect,useState } from 'react';
import Link from '@material-ui/core/Link';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import { useQuery } from '@apollo/react-hooks';
import gql from "graphql-tag";
// import CardMedia from '@material-ui/core/CardMedia';
import { red } from '@material-ui/core/colors';
import CardContent from '@material-ui/core/CardContent';
import { makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
// import SimplePaper from './SimplePaper'
import './App.css';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import * as am4plugins_forceDirected from "@amcharts/amcharts4/plugins/forceDirected"; 


const policyindex=  gql`   
query{
    policy_index{
    date,india,telangana
     }
}`


const useStyles = makeStyles((theme) => ({
    root: {
      maxWidth: '100%',
      flexGrow: 1,
    },header:{
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        height: 48,
        padding: '0 30px',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
      },
      headermain:{
        background: 'linear-gradient(45deg, #2196f3 30%, #21cbf3 90%)',
        boxShadow: '0 3px 5px 2px rgba(33, 203, 243, .30)',
        color: 'white',
      },
    media: {
      height: 0,
      paddingTop: '56.25%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: red[500],
    },
    paper: {
        padding: theme.spacing(0),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        margin: 'auto',
        
      },
  }));
export default function Page2() {
    const classes = useStyles();
    const { loading, error, data } = useQuery(policyindex);
    // const [state,setState]= useState('Confirmed');
   
       if (loading) {
         return <div className='loader_graph'>Loading...</div>;
       }
       if (error) {
         console.error(error);
         return  <div>Error</div>;
       }
   
    return (
<div>
    <br/><br/>

<CreateChart data={data.policy_index} />
<PolicyIndex data ={data.policy_index}/>
</div>
    );
}

am4core.useTheme(am4themes_animated);
function CreateChart(props) {
    const chart = useRef(null);
    const classes = useStyles();
    let itemtitle=''
    console.log(`In CreateChart: ${props.btnname}`)
    if (props.btnname==='Confirmed') {
     itemtitle ="Covid Confirmed Cases Trend"
    } else if(props.btnname==='Recovered'){
     itemtitle="Covid Recovered Cases Trend"
    } else {
     itemtitle="Covid Death Cases Trend"
    }
    useLayoutEffect(() => {
        var chart = am4core.create("chartdiv", am4plugins_forceDirected.ForceDirectedTree);

        var networkSeries = chart.series.push(new am4plugins_forceDirected.ForceDirectedSeries())
        
        networkSeries.data = [{
          name: 'Positive',
          children: [{
            name: 'Pos', value: 10
          }, {
            name: 'Positive', value: 6
          }
            ]
        }, {
          name: 'Negative',
            children: [{
              name: 'Neg', value: 10
            }, {
              name: 'Negative', value: 6
            }]}]
        
        networkSeries.dataFields.linkWith = "linkWith";
        networkSeries.dataFields.name = "name";
        networkSeries.dataFields.id = "name";
        networkSeries.dataFields.value = "value";
        networkSeries.dataFields.children = "children";
        networkSeries.links.template.distance = 1;
        networkSeries.nodes.template.tooltipText = "{name}";
        networkSeries.nodes.template.fillOpacity = 1;
        networkSeries.nodes.template.outerCircle.scale = 1;
        
        networkSeries.nodes.template.label.text = "{name}"
        networkSeries.fontSize = 8;
        networkSeries.nodes.template.label.hideOversized = true;
        networkSeries.nodes.template.label.truncate = true;
        networkSeries.minRadius = am4core.percent(2);
        networkSeries.manyBodyStrength = -5;
        networkSeries.links.template.strokeOpacity = 0;
  
      chart.current = chart;
  
      return () => {
        chart.dispose();
      };
    },[props.btnname,props.data] );
  
    return (
  
    <div>
   <Card className={classes.root}>
  
   <CardHeader className={classes.header}
    // style={{Color: "#000", Background: "blue"}}
          title="Twitter Sentiment"
          subheader="Hashtag #covid-19"
        />
   
   <Divider />
      <CardContent  id="chartdiv" style={{ width: "100%", height: "500px" }}/> 
      </Card>
  
      </div>
    );
  }

  function PolicyIndex(props) {
    const chart = useRef(null);
    const classes = useStyles();
    let itemtitle=''
    console.log(`In malefemale: ${JSON.stringify(props.data)}`)
    if (props.btnname==='Confirmed') {
     itemtitle ="Covid Confirmed Cases Trend"
    } else if(props.btnname==='Recovered'){
     itemtitle="Covid Recovered Cases Trend"
    } else {
     itemtitle="Covid Death Cases Trend"
    }
    useLayoutEffect(() => {
       
var mainContainer = am4core.create("PolicyIndex", am4core.Container);
mainContainer.width = am4core.percent(100);
mainContainer.height = am4core.percent(100);
mainContainer.layout = "horizontal";

var usData = props.data;

var maleChart = mainContainer.createChild(am4charts.XYChart);
maleChart.paddingRight = 0;
// maleChart.data = JSON.parse(JSON.stringify(usData));
maleChart.data=usData
console.log(`JSON.parse(JSON.stringify(usData)) : ${JSON.parse(JSON.stringify(usData))}`)
// Create axes
var maleCategoryAxis = maleChart.yAxes.push(new am4charts.CategoryAxis());
// let maleCategoryAxis = maleChart.yAxes.push(new am4charts.DateAxis());

maleCategoryAxis.dataFields.category = "date";
maleCategoryAxis.renderer.grid.template.location = 0;
maleCategoryAxis.renderer.inversed = true;
maleCategoryAxis.renderer.minGridDistance = 15;

var maleValueAxis = maleChart.xAxes.push(new am4charts.ValueAxis());
maleValueAxis.renderer.inversed = true;
maleValueAxis.min = 0;
maleValueAxis.max = 1;
maleValueAxis.strictMinMax = true;

maleCategoryAxis.dateFormatter = new am4core.DateFormatter();
maleCategoryAxis.dateFormatter.dateFormat  = "d MMM, yyyy";
// maleValueAxis.numberFormatter = new am4core.NumberFormatter();
// maleValueAxis.numberFormatter.numberFormat = "#.#'%'";

// Create series
var maleSeries = maleChart.series.push(new am4charts.ColumnSeries());
maleSeries.dataFields.valueX = "india";
// maleSeries.dataFields.valueXShow = "percent";
// maleSeries.calculatePercent = true;
maleSeries.dataFields.categoryY = "date";
// maleSeries.interpolationDuration = 1000;
maleSeries.columns.template.tooltipText = "India: {valueX}";
//maleSeries.sequencedInterpolation = true;


var femaleChart = mainContainer.createChild(am4charts.XYChart);
femaleChart.paddingLeft = 0;
// femaleChart.data = JSON.parse(JSON.stringify(usData));
femaleChart.data= usData
// Create axes
var femaleCategoryAxis = femaleChart.yAxes.push(new am4charts.CategoryAxis());
femaleCategoryAxis.renderer.opposite = true;
femaleCategoryAxis.dataFields.category = "date";
femaleCategoryAxis.renderer.grid.template.location = 0;
femaleCategoryAxis.renderer.minGridDistance = 15;

var femaleValueAxis = femaleChart.xAxes.push(new am4charts.ValueAxis());
femaleValueAxis.min = 0;
femaleValueAxis.max = 1;
femaleValueAxis.strictMinMax = true;
// femaleValueAxis.numberFormatter = new am4core.NumberFormatter();
// femaleValueAxis.numberFormatter.numberFormat = "#.#'%'";
femaleValueAxis.renderer.minLabelPosition = 0.01;

// Create series
var femaleSeries = femaleChart.series.push(new am4charts.ColumnSeries());
femaleSeries.dataFields.valueX = "telangana";
// femaleSeries.dataFields.valueXShow = "percent";
// femaleSeries.calculatePercent = true;
femaleSeries.fill = femaleChart.colors.getIndex(4);
femaleSeries.stroke = femaleSeries.fill;
//femaleSeries.sequencedInterpolation = true;
femaleSeries.columns.template.tooltipText = "Telangana: {valueX}";
femaleSeries.dataFields.categoryY = "date";
// femaleSeries.interpolationDuration = 1000;

  
      return () => {
        mainContainer.dispose();
      };
    },[props.data] );
  
    return (
  
    <div>
   <Card className={classes.root}>
  
   <CardHeader className={classes.header}
    // style={{Color: "#000", Background: "blue"}}
          title="Policy Index"
          subheader="Lesser the value better"
        />
   
   <Divider />
      <CardContent  id="PolicyIndex" style={{ width: "100%", height: "500px" }}/> 
      </Card>
  
      </div>
    );
  }
  